import { Button, Col, DatePicker, Divider, Form, message, Row, Select, Input } from "antd";
import {
  CHILDREN,
  DATE_FORMAT,
  MODAL_ADD_BUTTON_TYPE,
  NEW_DATE_FORMAT,
  SELECT_TEXT,
  UPDATE_TEXT,
  VALIDATION_FAILED,
} from "constants/CommonConstants";
import {
  ACTION_DATE,
  ALLOWED_BTN_LABELS,
  ALLOWED_STATUS,
  NEXT_ALLOCATION_START_DATE,
  SUCCESS_MESSAGE,
} from "constants/ManageProjects";
import { FORM_FIELDS, RESERVATION_FORM_FIELDS, START_DATE } from "constants/ManageProjectsConstant";
import useIntlMessage from "hooks/useIntlMessage";
import { capitalize } from "lodash";
import moment from "moment";
import { useMemo } from "react";
import { useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { setCountryForReserveEmployee, setDataForReserveEmployee } from "redux/actions";
import projectService from "services/ProjectService";
import UpdateStatus from "views/app-views/manage-group/projects/UpdateStatus";
import dashboardServices from "services/DashboardServices";
import employeeService from "services/EmployeeService";
import { useHistory } from "react-router-dom";
import { showLoader } from "redux/actions/Loader";
import Utils from "../../../../utils/index";

const formItemLayout = {
  labelCol: { span: 10 },
  wrapperCol: { span: 14 },
};

const AssignEmployees = props => {
  const empId = new URLSearchParams(props.location.search).get("empID");
  const status = new URLSearchParams(props.location.search).get("to");
  const reserveAccId = new URLSearchParams(props.location.search).get("reservedForAccountId");
  const dispatch = useDispatch();
  const { Option } = Select;
  const getMessage = useIntlMessage();
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const [projectList, setProjectList] = useState([]);
  const [subDomainList, setSubDomainList] = useState([]);
  const [showUpdateStatusModal, setShowUpdateStatusModal] = useState(false);
  const [isClickOnExtendable, setIsClickOnExtendable] = useState(false);
  const [isRequired, setIsRequired] = useState(false);
  const subDomains = useSelector(state => state.myTeams.subdomains);
  const domainList = useSelector(state => state.myTeams.domains);
  const previousCountry = useSelector(state => state.myTeams.reserveEmployeeCountry);
  const [selectedResourcesData, setSelectedResourcesData] = useState(null);
  const history = useHistory();
  const { TextArea } = Input;
  const internalBillable = "INTERNAL_BILLABLE";
  useEffect(() => {
    form.setFieldsValue({
      subDomainId: getSubDomainName,
      projectId: containsString(selectedResourcesData?.project?.projectName, "bench")
        ? null
        : selectedResourcesData?.project?.projectId,
      domainId: domainList?.find(item => item?.domainId === selectedResourcesData?.domain?.domainId)
        ? selectedResourcesData?.domain?.domainId
        : null,
    });
    if (selectedResourcesData?.nextAllocationStartDate !== undefined) {
      form.setFieldsValue({
        nextAllocationStartDate: moment(selectedResourcesData.nextAllocationStartDate),
      });
    }
  }, [selectedResourcesData, domainList]);

  useEffect(() => {
    validateFieldRequired();
    if (selectedResourcesData?.domain?.domainId) {
      handleOnDomainChange(selectedResourcesData?.domain?.domainId, true);
    }
  }, [selectedResourcesData]);

  useEffect(() => {
    if (!selectedResourcesData) {
      getEmployeeDetail(empId, status);
    }
  }, [selectedResourcesData, empId, status]);

  const getEmployeeDetail = async (empId, status) => {
    try {
      dispatch(showLoader(true));
      let params = { empId, status: status.toUpperCase() };
      let res = await dashboardServices.getEmployeeDetailByEmpIdAndStatus(params);
      if (reserveAccId) {
        res = res.filter(e => e?.reservedFor?.accountId === reserveAccId);
        setSelectedResourcesData(res[0]);
      } else {
        setSelectedResourcesData(res[0]);
      }
      if (["RESERVED", "SHADOW"].includes(res[0].allocationStatus)) {
        await getProjectListByAccountId(res[0]?.reservedFor?.accountId);
      } else {
        await getProjectListByAccountId(res[0]?.parentAccount?.accountId);
      }
    } catch (error) {
      console.error(error);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const onUpdateStatus = async (values, patch = false) => {
    try {
      setLoading(true);
      const payload = patch ? values : projectService.prepareAddAllocationPayload(values, selectedResourcesData);
      const res = patch
        ? await projectService.updateBillableAllocationDetails(payload)
        : await projectService.updateAllocationStatus(payload);
      if (res.statusCode === 200) {
        message.success(getMessage(SUCCESS_MESSAGE.UPDATE_STATUS.text));
        if (previousCountry) {
          await getAndUpdateEmployeeList();
        }
      }
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const getAndUpdateEmployeeList = async () => {
    const response = await dashboardServices.getDashboardAllocateEmployeesTo(status.toUpperCase(), previousCountry);
    const { columns, data, quickActions } = response;
    const employeeList = employeeService.prepareEmployeeList(data);
    dispatch(setDataForReserveEmployee(employeeList, quickActions, columns, status));
    dispatch(setCountryForReserveEmployee(previousCountry));
    history.push(`/app/manage/moveemployees?to=${status}`);
  };

  const handleUpdate = status => {
    form
      .validateFields()
      .then(async data => {
        const values = { ...data, allocationStatus: status };

        try {
          setLoading(true);
          await onUpdateStatus(values);
        } catch (error) {
          console.error(error);
        } finally {
          setLoading(false);
        }
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const getProjectListByAccountId = async accountId => {
    try {
      const projectList = await projectService.getListByAccountId(accountId);
      setProjectList(projectList);
    } catch (error) {
      console.error("Something went wrong", error);
    }
  };

  const disableBeforeDate = current => {
    const future = moment().subtract(0, "days");
    if (selectedResourcesData?.allocationStatus === "SHADOW") {
      const twoWeeksBack = moment().subtract(2, "weeks");
      return current < twoWeeksBack || current > future;
      //return current.isBefore(moment().startOf("today"));
    } else {
      return current?.isBefore(selectedResourcesData?.startDate || moment().startOf("month")) || current > future;
    }
  };
  const disableDateBeforeLastMonth = current => {
    return current?.isBefore(selectedResourcesData?.startDate || moment().startOf("month"));
  };
  const getStartDateLabel = () => {
    switch (selectedResourcesData?.allocationStatus) {
      case ALLOWED_STATUS.SHADOW.value:
        return `${getMessage("common.label.reserved")} ${getMessage(START_DATE.DATE.text)}`;
      case ALLOWED_STATUS.RESERVED.value:
        return `${getMessage("component.common.text.confirm")} ${getMessage(START_DATE.DATE.text)}`;
      case ALLOWED_STATUS.CONFIRMED.value:
        return `${getMessage("component.common.text.billable")} ${getMessage(START_DATE.DATE.text)}`;
      default:
        return `${getMessage("component.manageopenpool.billingstartdate.heading")}`;
    }
  };

  const getExpectedDateLabel = () => {
    return `${getMessage("common.action.expected.end.date.label")}`;
  };
  const containsString = (text, searchText) => {
    if (text) {
      return text.toLowerCase().includes(searchText.toLowerCase());
    }
  };

  const getSubDomainName = useMemo(() => {
    if (
      selectedResourcesData?.domain?.domainId &&
      subDomains?.find(subDomain => subDomain?.subDomainId === selectedResourcesData?.subDomain?.subDomainId)
    ) {
      return selectedResourcesData?.subDomain?.subDomainId;
    }
    return null;
  }, [selectedResourcesData, subDomains]);

  const handleOnDomainChange = (domainId, onFirsTime = false) => {
    const subDomainsLists = subDomains?.filter(subDomain => subDomain.domainId === domainId);
    setSubDomainList(subDomainsLists);
    if (!onFirsTime) {
      form.setFieldsValue({ [FORM_FIELDS.SUB_DOMAIN.value]: null });
    }
  };

  const handleProjectUpdate = async () => {
    form
      .validateFields()
      .then(async data => {
        const startDate = new Date(data?.nextAllocationStartDate);
        const payload = {
          employeeId: selectedResourcesData?.employeeId,
          reservedForAccountId: selectedResourcesData?.reservedFor?.accountId,
          allocationStatus: selectedResourcesData?.allocationStatus,
          assignedLevel: selectedResourcesData?.assignedLevel,
          projectId: data.projectId,
          domainId: data.domainId,
          subDomainId: data.subDomainId,
          nextAllocationStartDate:
            startDate.getTime() > 0 ? moment(data.nextAllocationStartDate).format(NEW_DATE_FORMAT).valueOf() : "",
        };
        try {
          setLoading(true);
          await onUpdateStatus(payload, true);
        } catch (error) {
          console.error(error);
        } finally {
          setLoading(false);
        }
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };
  const isReservedOrConfirmed = () => {
    if (
      selectedResourcesData?.allocationStatus === "RESERVED" ||
      selectedResourcesData?.allocationStatus === "CONFIRMED"
    ) {
      return true;
    }
    return false;
  };
  function disabledDate(current) {
    return (
      current.isBefore(selectedResourcesData?.startDate || moment().startOf("month")) ||
      current.isAfter(moment().add(1, "month").startOf("day"))
    );
  }
  const handleBillableUpdate = async () => {
    form
      .validateFields()
      .then(async data => {
        const payload = {
          employeeId: selectedResourcesData?.employeeId,
          reservedForAccountId: selectedResourcesData?.reservedFor?.accountId,
          allocationStatus: selectedResourcesData?.allocationStatus,
          projectId: data.projectId,
          domainId: data.domainId,
          subDomainId: data.subDomainId,
        };
        await onUpdateStatus(payload, true);
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const validateFieldRequired = () => {
    if (
      selectedResourcesData?.allocationStatus === ALLOWED_STATUS.BILLABLE.value ||
      selectedResourcesData?.allocationStatus === ALLOWED_STATUS.CONFIRMED.value
    ) {
      setIsRequired(true);
    } else if (
      form.getFieldValue(FORM_FIELDS.PROJECT_NAME.value) ||
      form.getFieldValue(FORM_FIELDS.DOMAIN_NAME.value) ||
      form.getFieldValue(FORM_FIELDS.SUB_DOMAIN.value)
    ) {
      setIsRequired(true);
    } else {
      setIsRequired(false);
      form.resetFields([FORM_FIELDS.PROJECT_NAME.value, FORM_FIELDS.DOMAIN_NAME.value, FORM_FIELDS.SUB_DOMAIN.value]);
      form.setFieldsValue({
        [FORM_FIELDS.PROJECT_NAME.value]: null,
        [FORM_FIELDS.DOMAIN_NAME.value]: null,
        [FORM_FIELDS.SUB_DOMAIN.value]: null,
      });
    }
  };

  return (
    <>
      <Form {...formItemLayout} layout={"horizontal"} form={form} labelAlign="left" requiredMark="">
        <Row gutter={12}>
          <Col className="text-left mb-1" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              label={getMessage(FORM_FIELDS.PARENT_ACCOUNT_LABEL.label)}
              name={FORM_FIELDS.PARENT_ACCOUNT_LABEL.name}
              className="my-1"
            >
              <span>
                {selectedResourcesData?.parentAccount?.accountName
                  ? selectedResourcesData?.parentAccount?.accountName
                  : "N/A"}
              </span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={getMessage(FORM_FIELDS.RESERVED_FOR_LABEL.label)}
              name={FORM_FIELDS.RESERVED_FOR_LABEL.value}
            >
              <span>
                {selectedResourcesData?.reservedFor?.accountName
                  ? selectedResourcesData?.reservedFor?.accountName
                  : "N/A"}
              </span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              label={getMessage(FORM_FIELDS.EMPLOYEE_ID.label)}
              name={FORM_FIELDS.EMPLOYEE_ID.name}
              className="my-1"
            >
              <span>{selectedResourcesData?.employeeId}</span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item className="my-1" label={getMessage(FORM_FIELDS.EMPLOYEE.label)} name={FORM_FIELDS.EMPLOYEE.name}>
              <span>{selectedResourcesData?.employeeName}</span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={getMessage(FORM_FIELDS.ALLOCATION_STATUS.label)}
              name={FORM_FIELDS.ALLOCATION_STATUS.name}
            >
              <span>{selectedResourcesData?.allocationStatus}</span>
            </Form.Item>
          </Col>
          {/* <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={getMessage(FORM_FIELDS.ASSIGNED_LEVEL.label)}
              name={FORM_FIELDS.ASSIGNED_LEVEL.name}
            >
              <span>{selectedResource?.assignedLevel}</span>
            </Form.Item>
          </Col> */}
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={getMessage(FORM_FIELDS.DESIGNATION.label)}
              name={FORM_FIELDS.DESIGNATION.name}
            >
              <span>{selectedResourcesData?.designation}</span>
            </Form.Item>
          </Col>
          <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
            <Form.Item
              className="my-1"
              label={`${
                selectedResourcesData?.allocationStatus === ALLOWED_STATUS.AVAILABLE.value
                  ? getMessage("component.common.status.available")
                  : capitalize(selectedResourcesData?.allocationStatus)
              } ${getMessage(START_DATE.DATE.text)}`}
            >
              <span>{moment(selectedResourcesData?.startDate).format(DATE_FORMAT)}</span>
            </Form.Item>
          </Col>
          {(selectedResourcesData?.allocationStatus === ALLOWED_STATUS.RESERVED.value ||
            selectedResourcesData?.allocationStatus === ALLOWED_STATUS.SHADOW.value) && (
            <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
              <Form.Item className="my-1" label={`${capitalize(selectedResourcesData?.allocationStatus)} End Date`}>
                <span>
                  {moment(
                    selectedResourcesData?.reservationExpiryDate || selectedResourcesData?.defaultReservedEndDate,
                  ).format(DATE_FORMAT)}
                </span>
              </Form.Item>
            </Col>
          )}
          {selectedResourcesData?.nextAllocationStartDate ? (
            <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
              <Form.Item
                className="my-1"
                label={getMessage(NEXT_ALLOCATION_START_DATE.label)}
                name={NEXT_ALLOCATION_START_DATE.key}
              >
                <span>{moment(selectedResourcesData.nextAllocationStartDate).format(DATE_FORMAT)}</span>
              </Form.Item>
            </Col>
          ) : null}
        </Row>
      </Form>

      <Divider />

      {!["extend"].includes(status) && (
        <Form
          {...formItemLayout}
          layout={"horizontal"}
          form={form}
          labelAlign="left"
          requiredMark=""
          onValuesChange={validateFieldRequired}
        >
          <Row gutter={12}>
            <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
              <Form.Item
                className="my-2"
                label={getMessage(FORM_FIELDS.PROJECT_NAME.label)}
                name={FORM_FIELDS.PROJECT_NAME.value}
                rules={[{ required: isRequired }]}
              >
                <Select
                  showArrow
                  showSearch
                  optionFilterProp={CHILDREN}
                  allowClear
                  disabled={status === "available"}
                  placeholder={getMessage(SELECT_TEXT.label, FORM_FIELDS.PROJECT_NAME.label)}
                >
                  {projectList?.map(e => {
                    return (
                      <Option key={e.projectId} value={e.projectId}>
                        {e.projectName}
                      </Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>
            <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
              <Form.Item
                className="my-1"
                name={FORM_FIELDS.DOMAIN_NAME.value}
                label={getMessage(FORM_FIELDS.DOMAIN_NAME.label)}
                rules={[{ required: isRequired }]}
              >
                <Select
                  showArrow
                  showSearch
                  optionFilterProp={CHILDREN}
                  allowClear
                  disabled={status === "available"}
                  placeholder={getMessage(SELECT_TEXT.label, FORM_FIELDS.DOMAIN_NAME.label)}
                  onChange={value => handleOnDomainChange(value)}
                >
                  {domainList?.map(e => {
                    return (
                      <Option key={e.domainId} value={e.domainId}>
                        {e.domainName}
                      </Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>
            <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
              <Form.Item
                className="my-1"
                name={FORM_FIELDS.SUB_DOMAIN.value}
                label={getMessage(FORM_FIELDS.SUB_DOMAIN.label)}
                rules={[{ required: isRequired }]}
              >
                <Select
                  placeholder={getMessage(SELECT_TEXT.label, FORM_FIELDS.SUB_DOMAIN.label)}
                  showArrow
                  allowClear
                  showSearch
                  optionFilterProp={CHILDREN}
                  disabled={status === "available"}
                >
                  {subDomainList?.map(e => {
                    return (
                      <Option key={e.subDomainId} value={e.subDomainId}>
                        {e.subDomainName}
                      </Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>
            {selectedResourcesData?.allocationStatus !== ALLOWED_STATUS.BILLABLE.value &&
            !["available", "extend"].includes(status) ? (
              <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
                <Form.Item
                  className="my-2"
                  label={getStartDateLabel()}
                  name={ACTION_DATE.START_DATE.value}
                  rules={[{ required: true }]}
                >
                  <DatePicker
                    disabledDate={isReservedOrConfirmed() ? disabledDate : disableBeforeDate}
                    format={DATE_FORMAT}
                    className={"w-100"}
                  />
                </Form.Item>
              </Col>
            ) : null}
            {["update"].includes(status) &&
              (selectedResourcesData?.allocationStatus === ALLOWED_STATUS.BILLABLE.value ||
                selectedResourcesData?.allocationStatus === internalBillable) && (
                <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    className="my-2"
                    label={getExpectedDateLabel()}
                    name={ACTION_DATE.BILLING_EXPECTED_END_DATE.value}
                  >
                    <DatePicker disabledDate={disableDateBeforeLastMonth} format={DATE_FORMAT} className={"w-100"} />
                  </Form.Item>
                </Col>
              )}
            {!["update", "available"].includes(status) && (
              <Col className="text-left" xs={24} md={12} lg={12} xl={12}>
                <Form.Item name={FORM_FIELDS.NOTES.name} label={getMessage(FORM_FIELDS.NOTES.label)}>
                  <TextArea rows={2} maxLength={500} placeholder={getMessage(FORM_FIELDS.NOTES.label)} />
                </Form.Item>
              </Col>
            )}
          </Row>
        </Form>
      )}

      <Row justify="end" className="mt-5">
        <Col>
          {selectedResourcesData?.allowedAllocationStatuses?.map(val => {
            if (status === "available" && val === ALLOWED_STATUS.AVAILABLE.value) {
              return (
                <Button
                  className="mr-3"
                  type={MODAL_ADD_BUTTON_TYPE.value}
                  key={RESERVATION_FORM_FIELDS.RESERVATION.name}
                  onClick={() => {
                    setShowUpdateStatusModal(true);
                    setIsClickOnExtendable(false);
                  }}
                >
                  {getMessage("component.common.text.release")}
                </Button>
              );
            } else if (status !== "available" && status !== "extend" && val !== "AVAILABLE") {
              return (
                <Button
                  className="mr-3"
                  key={UPDATE_TEXT.value}
                  type={MODAL_ADD_BUTTON_TYPE.value}
                  onClick={() => handleUpdate(val)}
                  loading={loading}
                >
                  {getMessage(ALLOWED_BTN_LABELS[val])}
                </Button>
              );
            }
          })}

          {/* {(!selectedResourcesData?.isExtendable && selectedResourcesData?.reservedDateExtensionCount === 2) && (
          )} */}
          {status === "update" && (
            <Button
              className="mr-3"
              key={UPDATE_TEXT.value}
              type={MODAL_ADD_BUTTON_TYPE.value}
              onClick={() => handleProjectUpdate()}
              loading={loading}
            >
              {getMessage(ALLOWED_BTN_LABELS["UPDATE"])}
            </Button>
          )}
          {status === "extend" && selectedResourcesData?.isExtendable && (
            <Button
              className="mr-3"
              type={MODAL_ADD_BUTTON_TYPE.value}
              key={RESERVATION_FORM_FIELDS.RESERVATION.name}
              onClick={() => {
                setShowUpdateStatusModal(true);
                setIsClickOnExtendable(true);
              }}
            >
              {getMessage(RESERVATION_FORM_FIELDS.RESERVATION.extendText)}
            </Button>
          )}
        </Col>
      </Row>

      {showUpdateStatusModal && (
        <UpdateStatus
          moveTo={true}
          visible={showUpdateStatusModal}
          selectedResource={selectedResourcesData}
          isClickOnExtendable={isClickOnExtendable}
          getAndUpdateEmployeeList={getAndUpdateEmployeeList}
          onCancel={() => {
            setShowUpdateStatusModal(false);
            setIsClickOnExtendable(false);
          }}
        />
      )}
    </>
  );
};

const mapStateToProps = ({ myTeams }) => {
  return { employees: myTeams.employeeData };
};
export default connect(mapStateToProps)(AssignEmployees);
