import { Button, Divider, Steps } from "antd";
import { APP_PREFIX_PATH } from "configs/AppConfig";
import { Link, useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { resetFilterSort } from "redux/actions";
import AllocateEmployees from "./AllocateEmployees";
import ReserveEmployees from "./AssignEmployees";
import BulkAllocate from "./BulkAllocate";
import { useEffect } from "react";

const Employees = ({ location }) => {
  let history = useHistory();
  const dispatch = useDispatch();
  const { Step } = Steps;
  const params = new URLSearchParams(location.search).get("to");
  const empStatus = new URLSearchParams(location.search).get("empStatus");

  useEffect(() => {
    return () => {
      if (history.location.pathname.includes("moveemployees") === false) {
        dispatch(resetFilterSort());
      }
    };
  }, []);

  const onChange = () => {
    history.push(`${APP_PREFIX_PATH}/manage/moveemployees?to=${params}`);
  };

  return (
    <>
      <Steps current={1} onChange={onChange}>
        <Step title="Step 1" description="Select Employee" />
        <Step title="Step 2" description="Allocate Employee" />
      </Steps>
      <Button className="mt-3">
        <Link to={`${APP_PREFIX_PATH}/manage/moveemployees?to=${params}`}>Previous</Link>
      </Button>

      <Divider />
      {["available", "billable", "confirmed", "extend", "update", "internalBillable"].includes(params) || empStatus ? (
        <ReserveEmployees location={location} />
      ) : (
        // <BulkAllocate location={location}/>
        <AllocateEmployees location={location} />
      )}
    </>
  );
};

export default Employees;
